import React from "react";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, Theme, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Swap from "../Swap/Swap";
import Operate from "../Operate/Operate";
import { Web3Provider } from "../../ethereum";
import Button from "@material-ui/core/Button";

interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography component={"span"}>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: any) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
    'max-width': 500,
    "box-shadow": "0px 0px 16px 6px rgba(0, 0, 139, .4)",
    position:'absolute',
    top: '20%'
  },
}));

export default function Widget(props: {
  web3: Web3Provider;
  connectWallet: () => void;
}) {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index: number) => {
    setValue(index);
  };

  const connectWalletButton =
    props.web3 !== null ? undefined : (
      <Button
        variant="contained"
        color="primary"
        size="large"
        onClick={props.connectWallet}
      >
        Connect Wallet
      </Button>
    );

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
        >
          <Tab label="Swap" {...a11yProps(0)} />
          <Tab label="Operate" {...a11yProps(1)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <Swap web3={props.web3} />
          {connectWalletButton}
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <Operate web3={props.web3} />
          {connectWalletButton}
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}
